@import url(https://fonts.googleapis.com/css2?family=Shojumaru&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Wallpoet&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    width: 100vw;
    max-width: 800px;
    margin: auto;
}


@media (min-width: 750px) {
    .App {
        width: 60vw;
        max-width: 800px;
        height: 100%;
    }
}

.Logo {
    width: 100vw;
    height: 50px;
    font-size: 40px;
    font-family: 'Shojumaru', cursive;
    text-align: center;
    margin: 10px 0 0 0;
}

@media (min-width: 750px) {
    .Logo {
        width: 100%;
        height: 80px;
        font-size: 70px;
        font-family: 'Shojumaru', cursive;
        text-align: center;
        margin: 60px 0 0 0;
    }
}
.Eyes {
    width: 100vw;
    height: 60px;
    display: flex;
    justify-content: center;
    margin: 30px auto 0 auto;
}

.LeftEye {
    height: 30px;
    width: 30px;
    border: 3px solid #000;
    border-radius: 50%;
    background-color: #000;
    font-size: 0;
    margin: 0 15%;
}

.RightEye {
    height: 30px;
    width: 30px;
    border: 3px solid #000;
    border-radius: 50%;
    background-color: #000;
    font-size: 0;
    margin: 0 15%;
}

@media (min-width: 750px) {
    .Eyes {
        width: 100%;
        height: 60px;
        display: flex;
        justify-content: center;
        margin: 30px auto 0 auto;
    }


    .LeftEye {
        height: 40px;
        width: 40px;
        border: 3px solid #000;
        border-radius: 50%;
        background-color: #000;
        font-size: 0;
        display: inline-flex;   
        margin: 0 15%;

    }
    
    .RightEye {
        height: 40px;
        width: 40px;
        border: 3px solid #000;
        border-radius: 50%;
        background-color: #000;
        font-size: 0;
        display: inline-flex;
        margin: 0 15%;
    }
}
.Mouth {
    height: 240px;
    width: 60%;
    border: 30px solid #000;
    border-radius: 50%;
    margin: 0 auto;
    z-index: 1;
}

.MouthMunching {
    animation: munchingMouth 1s;
    animation-iteration-count: 10;
}

@keyframes munchingMouth {
    0% {height: 240px}
    50% {height: 120px}
    100% {height: 240px}
}

@media (min-width: 750px) {
    .Mouth {
        height: 390px;
        max-width: 400px;
        border: 30px solid #000;
        border-radius: 50%;
        margin: 0 auto;
        z-index: 1;
    }

    @keyframes munchingMouth {
        0% {height: 390px}
        50% {height: 195px}
        100% {height: 390px}
    } 
}

.UpperTeethWrapper {
    height: 120px;
    display: flex;
    justify-content: center;
}

.UpperTooth {
    height: 20px;
    width: 20px;
    border: 3px solid #000;
    margin: 0 1px;
    border-radius: 30px 30px 5px 5px;
    margin-bottom: 25px;
    position: relative;
    z-index: -1;
}

@media (min-width: 750px) {
    .UpperTeethWrapper {
        height: 195px;
        display: flex;
        justify-content: center;
    }
    
    .UpperTooth {
        height: 30px;
        width: 30px;
        border: 3px solid #000;
        margin: 0 1px;
        border-radius: 30px 30px 5px 5px;
        margin-bottom: 25px;
        position: relative;
        z-index: -1;
    }
}
.LowerTeethWrapper {
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.LowerTooth {
    height: 20px;
    width: 20px;
    border: 3px solid #000;
    margin: 0 1px;
    border-radius: 5px 5px 30px 30px;
    margin-top: 25px;
    z-index: -1;
}

.LowerMunching {
    animation: munchingLower 1s;
    animation-iteration-count: 10;
}

@keyframes munchingLower {
    0% {transform: translateY(0)}
    50% {transform: translateY(-120px)}
    100% {transform: translateY(0)}
}

@media (min-width: 750px) {
    .LowerTeethWrapper {
        height: 170px;
        display: flex;
        justify-content: center;
        align-items: flex-end;
    }
    
    .LowerTooth {
        height: 30px;
        width: 30px;
        border: 3px solid #000;
        margin: 0 1px;
        border-radius: 5px 5px 30px 30px;
        margin-top: 25px;
        z-index: -1;
    }

    @keyframes munchingLower {
        0% {transform: translateY(0)}
        50% {transform: translateY(-195px)}
        100% {transform: translateY(0)}
    }
}

.Button {
    background: #000000;
    color: #fff;
    cursor: pointer;
    font-family: 'Poppins', sans-serif;
    font-size: 24px;
    border-radius: 10px;
}

.Button:hover {
    background-color: #226d31;
}

.ButtonDisabled {
    background: #000000;
    color: #fff;
    font-family: 'Poppins', sans-serif;
    font-size: 24px;
    cursor: not-allowed;
    border-radius: 10px;
}

.Form {
    display: flex;
    justify-content: center;
    height: 50px;
    margin-bottom: 35px;
}

.Form input {
    font-size: 16px;
}

.DefaultText {
    font-size: 20px;
    display: flex;
    justify-content: center;
    margin: 0 35px 35px 35px;
    line-height: 26px;
}

.GuessSucess {
    font-size: 20px;
    margin: 0 35px 35px 35px;
    line-height: 26px;
    display: flex;
    justify-content: center;
    color: green;
}

.GuessInvalid {
    font-size: 20px;
    margin: 0 35px 35px 35px;
    line-height: 26px;
    display: flex;
    justify-content: center;
    color: red;
}

.ShowFactsBtn {
    display: flex;
    justify-content: center;
}

.ShowFactsBtn button {
    height: 80px;
    border: 2px solid #01c177;
    background-color: #01c177;
    border-radius: 10px;
    color: #000;
    font-size: 14px;
}

.ShowFactsBtn button:hover {
    color: #fff;
    background-color: #01c177;
    cursor: pointer;
    color: 000;
}

@media (min-width: 750px) {
    .Form {
        height: 50px;
        margin: 0 auto 30px auto;
    }
    
    .Form input {
        font-size: 16px;
    }
    
    .DefaultText {
        font-size: 20px;
        margin: 0 auto 30px auto;
    }
    
    .GuessSucess {
        font-size: 20px;
        margin: 0 auto 30px auto;
        color: #01c177;
    }
    
    .GuessInvalid {
        font-size: 20px;
        margin: 0 auto 30px auto;
        color: red;
    }
    
    .ShowFactsBtn {
        margin: 0px auto 30px auto;
    }
    
    .ShowFactsBtn button {
        height: 70px;
        border: 2px solid #01c177;
        border-radius: 10px;
        background: none;
        font-size: 14px;
    }
    
    .ShowFactsBtn button:hover {
        color: #fff;
        background-color: #01c177;
        cursor: pointer;
    }
}





.lds-hourglass {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}
.lds-hourglass:after {
    content: " ";
    display: block;
    border-radius: 50%;
    width: 0;
    height: 0;
    margin: 0px;
    box-sizing: border-box;
    border: 70px solid #01c177;;
    border-color: #01c177 transparent #01c177 transparent;
    animation: lds-hourglass 1.2s infinite;
    }
    @keyframes lds-hourglass {
    0% {
        transform: rotate(0);
        animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
    }
    50% {
        transform: rotate(900deg);
        animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    }
    100% {
        transform: rotate(1800deg);
    }
}
